import React, { useState } from "react";
import "./Footer.css";

const Footer = () => {
  const [click, setClick] = useState(false);

  const handleClickFooter = () => setClick(!click);

  return (
    <div className="footer">
      <div className="footer-all">
        <div className="footer-up">
          <div className="footer-links">
            <p className="footer-head">
              ANUP
              <i class="fab fa-typo3" />
            </p>
            <div className="footer-links-all">
              <a href="/#">Teardown</a>
              <a href="/#">News</a>
              <a href="/#">Partners</a>
              <a href="/#">About Us</a>
              <a href="/#">Contact Us</a>
              <a href="/#">Terms of Use</a>
            </div>
          </div>

          <div className="footer-links">
            <p className="footer-head">Social</p>
            <div className="footer-links-all">
              <a href="/#">Facebook</a>
              <a href="/#">Twitter</a>
              <a href="/#">Youtube</a>
            </div>
          </div>

          <div className="footer-links">
            <p className="footer-head">Service</p>
            <div className="footer-links-all">
              <a href="/#">Compare</a>
              <a href="/#">Download</a>
              <a href="/#">Feedback</a>
              <a href="/#">Bug Report</a>
            </div>
          </div>

          <div className="footer-links">
            <p className="footer-head">Activity</p>
            <div className="footer-links-all">
              <a href="/#">Influencers</a>
              <a href="/#">Affilate</a>
              <a href="/#">Co-branding</a>
              <a href="/#">Honoe</a>
              <a href="/#">Giveaway</a>
            </div>
          </div>

          <div className="footer-last">
            <p className="footer-head-last">Newsletter Subscription</p>
            <div className="footer-subscribe">
              <p className="footer-links-para">Subscribe to our newsletter</p>
              <button
                className={click ? "btn-footer-paraclick" : "btn-footer-para"}
                onClick={handleClickFooter}
              >
                {click ? "SUBSCRIBED" : "SUBSCRIBE"}
              </button>
            </div>
          </div>
        </div>
        <div className="footer-end">
          <p className="footer-para-end">
            Copyright@ 2020 ANUP. All rights reserved.
          </p>
        </div>
      </div>
    </div>
  );
};

export default Footer;
