import React from "react";
import "./Touch.css";

const Touch = () => {
  return (
    <div className="touch">
      <div className="touch-full">
        <h1 className="touch-head">Get In Touch</h1>
        <div className="touch-main">
          <div className="touch-first-part">
            <form className="form-all">
              <div className="form-part">
                <input
                  type="text"
                  className="form-group"
                  placeholder="Your Name"
                />
              </div>
              <div className="form-part">
                <input
                  type="number"
                  className="form-group"
                  placeholder="Phone no."
                />
              </div>
              <div className="form-part">
                <input
                  type="email"
                  className="form-group"
                  placeholder="Email Id"
                />
              </div>
              <div className="form-part">
                <textarea
                  className="form-group-text"
                  placeholder="Your Message"
                  rows="4"
                ></textarea>
              </div>
              <button type="submit" className="btn-form">
                Send Message
              </button>
            </form>
          </div>

          <div className="touch-second-part">
            <div className="follow">
              <b>Address:</b>
              <i className="fa fa-map-marker"></i>Ring Road,Kathmandu,Nepal
            </div>
            <div className="follow">
              <b>Phone:</b>
              <i className="fa fa-phone"></i>+977 9845810546
            </div>
            <div className="follow">
              <b>Email:</b>
              <i className="fa fa-envelope-o"></i>anuplamsal7410@gmail.com
            </div>
            <div className="follow">
              <label>
                <b>Get Social:</b>
              </label>
              <a href="/#" className="touch-link">
                <i className="fa fa-facebook"></i>
              </a>
              <a href="/#" className="touch-link">
                <i className="fa fa-youtube-play"></i>
              </a>
              <a href="/#" className="touch-link">
                <i className="fa fa-twitter"></i>
              </a>
              <a href="/#" className="touch-link">
                <i className="fa fa-instagram"></i>
              </a>
              <a href="/#" className="touch-link">
                <i className="fa fa-google-plus"></i>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Touch;
