import React from "react";
import img from "../../img/img1.jpeg";
import Button from "../button/Button";
import "./About.css";

const About = () => {
  return (
    <section className="About">
      <div className="about-items">
        <div className="about-desc">
          <h1>Tired of your own life</h1>
          <p>
            It's easy to be positive when everything is working out. It's much
            harder, much much harder when nothing is working out. But that's
            when we need it the most ... Everything is worth the prize. You
            cannot let a fear of failure or a fear of comparison or a fear of
            judgment stop you from doing the things that will make you great.
            You cannot succeed without the risk of failure. You cannot have a
            voice without the risk of criticism. You cannot love without the
            risk of loss. You must take these risks.
          </p>
        </div>
        <Button buttonStyle="btn--outline">Learn More</Button>
        <div className="btn-mobile">
          <button className="btn-mobile-first">Learn More</button>
        </div>
      </div>
      <div className="img-about">
        <img src={img} alt="img" className="img-first" />
      </div>
    </section>
  );
};

export default About;
