import React, { Component } from 'react';
import Button from '../button/Button';
import {MenuItems} from "./MenuItems";
import "./Navbar.css";

class Navbar extends Component {
    state = {
        clicked: false,
    }

    handleClick = () => this.setState({
        clicked: !this.state.clicked,
    })

    closeMobileMenu = () => this.setState({
        clicked: false,
    })
    render() {
        return (
            <nav className="Navbar">
                <h1 className="Navbar-logo" onClick={this.closeMobileMenu}>
                    ANUP<i class="fab fa-typo3"/>
                </h1>
                <div className="menu-icon" onClick={this.handleClick}>
                    <i className={this.state.clicked ? "fas fa-times" : "fas fa-bars"}/>
                </div>
                <ul className={this.state.clicked ? "menu-item active" : "menu-item"}>
                    {
                        MenuItems.map((val, ind) => {
                            return(
                                <li key={ind}><a href={val.url} className={val.cName}>{val.title}</a></li>
                            );
                        })
                    }
                </ul>
                <Button>Sign Up</Button>
            </nav>
        );
    }
}

export default Navbar;