import React from "react";
import PriceMenu from "./PriceMenu";
import PricePart from "./PricePart";
import "./Price.css";

const Price = () => {
  return (
    <div className="price">
      <div className="price-full">
        <h1 className="price-title">Price Plan</h1>
        <div className="price-plans">
          {PriceMenu.map((val, ind) => {
            return (
              <PricePart
                head={val.head}
                rate={val.rate}
                cFirst={val.cFirst}
                cSecond={val.cSecond}
                cThird={val.cThird}
                cForth={val.cForth}
                cFifth={val.cFifth}
                dFirst={val.dFirst}
                dSecond={val.dSecond}
                dThird={val.dThird}
                dForth={val.dForth}
                dFifth={val.dFifth}
                btnName={val.btnName}
                key={ind}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Price;
