import React from 'react';
import "./Team.css";

const TeamPart = (props) => {
    return (
        <div className="team-intro">
        <div className="img-team">
            <img src={props.img} alt="img" className="img-all" />
          </div>
          <div className="team-bio">
            <h2>{props.head}</h2>
            <p className="title">{props.paraFirst}</p>
            <p>{props.paraSecond}</p>
            <p>{props.paraThird}</p>
              <button className="team-btn">{props.btn}</button>
          </div>
      </div>
    );
}

export default TeamPart;