import React from "react";
import TeamMenu from "./TeamMenu";
import TeamPart from "./TeamPart";
import "./Team.css";

const Team = () => {
  return (
    <div className="team">
      <h1 className="top-title">Our Team</h1>
      <div className="team-full">
      {TeamMenu.map((val, ind) => {
        return (
          <TeamPart
            img={val.img}
            head={val.head}
            paraFirst={val.paraFirst}
            paraSecond={val.paraSecond}
            paraThird={val.paraThird}
            btn={val.btn}
            key={ind}
          />
        );
      })}
      </div>
    </div>
  );
};

export default Team;
