import React from "react";

const PricePart = (props) => {
  return (
    <div className="price-details">
      <div className="price-details-title">
        <h2>{props.head}</h2>
        <p className="details-para">
          {props.rate}
          <span>month</span>
        </p>
      </div>
      <div className="price-details-content">
        <ul className="price-details-list">
          <li>
            <i className={props.cFirst}></i>
            {props.dFirst}
          </li>
          <li>
            <i className={props.cSecond}></i>
            {props.dSecond}
          </li>
          <li>
            <i className={props.cThird}></i>
            {props.dThird}
          </li>
          <li>
            <i className={props.cForth}></i>
            {props.dForth}
          </li>
          <li>
            <i className={props.cFifth}></i>
            {props.dFifth}
          </li>
        </ul>
      </div>
      <div className="btn-details">
        <button className="buy-btn">{props.btnName}</button>
      </div>
    </div>
  );
};

export default PricePart;
